@import '~scss/variables';

$fp-point-size-active: 24px;
$fp-point-size-visual: 18px;

floor-plan {

  .fp-grid {
    justify-content: flex-start;

    @media (min-width: $screen-lg) {
      flex-wrap: nowrap;
    }
  }

  .ff-tip {
    width: 310px;
    padding: 1rem;
    font-family: var(--font-family-major);
    font-size: inherit;

    ff-header {
      padding: 0 0 .3rem 0;
      //font-size: 1rem;
    }

    .ff-label, .ff-control {
      min-height: 2rem;
    }
  }

  .fp-point {
    position: absolute;
    width: $fp-point-size-active;
    height: $fp-point-size-active;
    transform: translate3d(-50%, -50%, 0);
    border-radius: 50%;
    cursor: pointer;

    &::before {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      width: $fp-point-size-visual;
      height: $fp-point-size-visual;
      transform: translate3d(-50%, -50%, 0);
      background-color: #000;
      border-radius: 50%;
      border: 2px solid #fff;
      box-shadow: 0 0 .5rem rgba(0, 0, 0, .3);
      transition: box-shadow .15s, border-width .15s;
    }

    &:hover {
      z-index: 1;
      &::before {
        box-shadow: 0 0 .5rem rgba(0, 0, 0, .8);
        z-index: 2;
      }
    }

    &.available::before {
      background-color: var(--brand-color);
    }

    &.selected::before {
      border-width: 4px;
      box-shadow: 0 0 1rem 1px #000;
    }

    &.invalid::before {
      background-color: var(--danger-color);
    }
  }


  .fp-image {
    display: block;
    position: relative;
    max-width: 700px;

    & > img:first-of-type { // thumbnail and .img-hq
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }

    & > img:last-of-type { // thumbnail and .img-hq
      position: relative;
      max-width: 100%;
      max-height: 600px;
      width: auto;
      height: auto;
    }
  }

  .fp-points {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    cursor: crosshair;
  }

  .ff-bulk-read-only & .fp-points {
    cursor: auto;
  }
}

floor-plan-point-details, floor-plan-point-settings {
  display: flex;

  ff-panel {
    flex: auto;
  }
}

.fp-user-container {
  position: relative;
  align-self: stretch;
  justify-content: center;
  padding: .5rem 0;
}

.fp-avatar {
  margin: .5rem .5rem .5rem 0;
  img {
    border-radius: 50%;
  }
}

.fp-user-name {
  font-weight: bold;
  font-size: 1.15em;
}

.fp-user-company {
  margin-top: .25rem;
}

.fp-btn-more {
  margin: 0 -.333rem -.5rem auto;

  .ff-btn.ff-btn {
    min-height: 24px;
    padding: .2rem .3rem;

    span:first-of-type:not(.fa) {
      margin: 0;
    }

    .fa.fa {
      display: none;
    }
  }
}

.fp-descr {
  .ff-static.ff-static {
    padding-top: .5rem;
    padding-bottom: .5rem;
    line-height: 1.25;
  }
}

.fp-unit-photo {
  display: block;
  margin-top: .5rem;
}

.fp-legend {
  align-items: center;
  padding-top: .75rem;
  padding-bottom: .75rem;
  font-size: .85rem;
  font-weight: bold;
  text-transform: uppercase;

  .available, .unavailable {
    margin: 1px 0;
    font-size: .65rem;

    &::before {
      content: '';
      display: inline-block;
      width: 10px;
      height: 10px;
      margin: 0 5px 1.5px 0;
      vertical-align: middle;
    }
  }

  .available::before {
    background-color: #fff;
    border: 1px solid;
  }

  .unavailable::before {
    background-color: var(--brand-color);
  }
}
